import {Component, Inject, Input, OnInit} from '@angular/core';
import {SpinnerService, UserDetails, UserProfileService} from '../core';
import {CookieService} from 'ngx-cookie-service';
import {DOCUMENT} from '@angular/common';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.less']
})
export class HeaderComponent implements OnInit {
  @Input() userDetails: UserDetails;
  redirectUrl: string;
  public redirectLaunchLabel;
  private launchCookie = 'CONSOLE_LAUNCH_SOURCE';
  private defaultLaunchCookie = 'mdm';

  constructor(private cookieService: CookieService, @Inject(DOCUMENT) private document: any,
              private userProfileService: UserProfileService, private spinnerService: SpinnerService) {
  }

  ngOnInit() {
    this.redirectUrl = this.cookieService.get('ORIGINATION_URL') ? this.cookieService.get('ORIGINATION_URL') : '';
    this.redirectLaunchLabel = this.cookieService.get(this.launchCookie) === this.defaultLaunchCookie
      ? 'back.to.intelligent.hub' : 'back.to.hub.config';
  }

  goBack() {
    this.userProfileService.doLogOut$().subscribe((response) => {
      this.document.location.href = response;
    }, (error) => {
    });
  }
}
