import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {UserProfileService} from './user-profile.service';
import {ApiService} from './api.service';
import {HttpService} from './http.service';

export interface Url {
  href: string;
}

export interface ApiResource {
  consoleUserDetails: Url;
  HubBranding: Url;
  consoleUemIntegration: Url;
}

@Injectable({
  providedIn: 'root'
})

export class BootstrapService {
  private apiResource: Observable<any>;

  constructor(private apiService: ApiService, private httpService: HttpService,
              private userProfileService: UserProfileService) {
    this.apiResource = this.apiService.getApiList();
  }

  load(): Promise<any> {
    const self = this;
    return new Promise(resolve => {
      this.apiResource
        .subscribe(ApiResponse => {
          self.apiService.setApiResource(ApiResponse['_links']);
          self.userProfileService.getUserDetails()
            .subscribe((userData) => {
              self.userProfileService.setUserDetails(userData);
              resolve();
            }, () => {
              self.userProfileService.setUserDetails({});
              resolve();
            });
        }, () => {
          self.userProfileService.setUserDetails({});
          resolve();
        });
    });
  }
}
