import {BrowserModule} from '@angular/platform-browser';
import {APP_INITIALIZER, NgModule} from '@angular/core';
import {ClarityModule} from '@clr/angular';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {HttpClientModule, HttpClient, HTTP_INTERCEPTORS} from '@angular/common/http';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {RouterModule} from '@angular/router';
import {AppComponent} from './app.component';
import {HeaderComponent} from './header/header.component';
import {LeftNavComponent} from './left-nav/left-nav.component';
import {AppRoutingModule} from './app-routing.module';
import {BootstrapService, CoreModule, ConsoleHttpInterceptor, AuthGuardService} from './core';
import {ErrorComponent} from './error/error.component';
import {SharedModule} from './shared';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {WindowService} from './core/services/window.service';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, '/catalog-portal/console/assets/i18n/', '.json');
}

export function bootstrapProviderFactory(provider: BootstrapService) {
  return () => provider.load();
}

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    LeftNavComponent,
    ErrorComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    ClarityModule,
    HttpClientModule,
    AppRoutingModule,
    CoreModule,
    SharedModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    })
  ],
  exports: [RouterModule, TranslateModule],
  providers: [
    AuthGuardService,
    WindowService,
    {provide: APP_INITIALIZER, useFactory: bootstrapProviderFactory, deps: [BootstrapService], multi: true},
    {provide: HTTP_INTERCEPTORS, useClass: ConsoleHttpInterceptor, multi: true}
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}
