import {Injectable} from '@angular/core';
import {
  HttpInterceptor,
  HttpHandler,
  HttpResponse,
  HttpRequest,
  HttpEvent,
  HttpErrorResponse
} from '@angular/common/http';
import {CookieService} from 'ngx-cookie-service';
import {Observable} from 'rxjs';
import {map, catchError} from 'rxjs/operators';
import {Router} from '@angular/router';

import {UserProfileService} from './user-profile.service';
import {ApiService} from './api.service';

@Injectable()
export class ConsoleHttpInterceptor implements HttpInterceptor {
  private refreshingUccCookie: boolean;

  constructor(private apiService: ApiService, private router: Router,
              private cookieService: CookieService, private userProfileService: UserProfileService) {
    this.refreshingUccCookie = false;
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const req = this.addXSRFHeader(request);
    return next.handle(req).pipe(map(res => {
      if (res instanceof HttpResponse) {
        return res;
      }
    }), catchError((err: HttpErrorResponse) => {
      if (err.status === 401) {
        this.http401Interceptor(err);
      }
      if (err.status === 403 || err.status === 409) {
        this.http403Interceptor(err);
      }
      throw err;
    }));
  }

  private addXSRFHeader(request: HttpRequest<any>): HttpRequest<any> {
    let XSRFHeader: string;
    XSRFHeader = this.cookieService.get('EUC_XSRF_TOKEN') ? this.cookieService.get('EUC_XSRF_TOKEN') : '';
    return request.clone({
      headers: request.headers.set('X-XSRF-TOKEN', XSRFHeader)
    });
  }

  private http401Interceptor(err) {
    this.userProfileService.setUserDetails(false);
    this.router.navigate(['error']);
  }

  private http403Interceptor(err) {
    this.userProfileService.setUserDetails(false);
    this.router.navigate(['error']);
  }
}
