import {Injectable, Optional, SkipSelf} from '@angular/core';
import {Subject} from 'rxjs';
import {TranslateService} from '@ngx-translate/core';

export interface ToastMessage {
  message: string;
  type: string;
}

@Injectable()
export class ToastService {
  private toastSubject = new Subject<ToastMessage>();

  toastState = this.toastSubject.asObservable();

  constructor(@Optional() @SkipSelf() prior: ToastService, private translate: TranslateService) {
    if (prior) {
      return prior;
    }
  }

  activate(message?: string, type?: string) {
    this.toastSubject.next(<ToastMessage>{message: message || '', type: type || 'info'});
  }

  activateWithTranslate(message?: string, type?: string) {
    this.translate.get(message).subscribe(msg => {
      this.toastSubject.next(<ToastMessage>{message: msg || '', type: type || 'info'});
    });
  }
}
