import { DOCUMENT } from '@angular/common';
import {Component, Inject, OnInit} from '@angular/core';
import { WindowService } from '../core/services/window.service';

@Component({
  selector: 'app-error',
  templateUrl: './error.component.html',
  styleUrls: ['./error.component.less']
})
export class ErrorComponent implements OnInit {

  public isMdmConfigured = this.windowService.getHubConsole().mdmRedirectUrl;
  public errorMessageCode = this.windowService.getHubConsole().errorMessageCode;
  constructor(
    private windowService: WindowService,
    @Inject(DOCUMENT) private document: any
  ) {}

  ngOnInit() {
  }

  public redirect(): void {
    this.document.location.href = this.windowService.getHubConsole().mdmRedirectUrl;
  }

}
