import {Injectable} from '@angular/core';
import {
  CanActivate,
  CanActivateChild,
  CanLoad,
  Router,
} from '@angular/router';

import {UserProfileService} from './user-profile.service';
import {WindowService} from './window.service';

@Injectable()
export class AuthGuardService implements CanActivate, CanActivateChild, CanLoad {
  constructor(private userProfileService: UserProfileService, private router: Router, private windowService: WindowService) {
  }

  canLoad() {
    if (this.windowService.getHubConsole().mdmRedirectUrl || this.windowService.getHubConsole().errorMessageCode) {
      this.router.navigate(['/error']);
      return false;
    }

    if (this.userProfileService.isLoggedIn) {
      return true;
    }
    this.router.navigate(['/error']);
    return false;
  }

  canActivate() {
    if (this.windowService.getHubConsole().mdmRedirectUrl || this.windowService.getHubConsole().errorMessageCode) {
      this.router.navigate(['/error']);
      return false;
    }
    if (this.userProfileService.isLoggedIn) {
      return true;
    }
    this.router.navigate(['/error']);

    return false;
  }

  canActivateChild() {
    return this.canActivate();
  }
}
