import {Component, OnDestroy, OnInit} from '@angular/core';
import {ToastService} from './toast.service';

import {Subscription} from 'rxjs';

@Component({
  selector: 'app-toast',
  templateUrl: './toast.component.html',
  styleUrls: ['./toast.component.less']
})
export class ToastComponent implements OnDestroy, OnInit {
  private defaults = {
    message: '',
    type: 'info'
  };
  private toastElement: any;
  private toastSubscription: Subscription;
  type: string;
  message: string;

  constructor(private toastService: ToastService) {
    this.toastSubscription = this.toastService.toastState.subscribe((toastMessage) => {
      this.activate(toastMessage.message, toastMessage.type);
    });
  }

  activate(message = this.defaults.message, type = this.defaults.type) {
    this.message = message;
    this.type = type;
    this.show();
  }

  ngOnInit() {
    this.toastElement = document.getElementById('toast');
  }

  ngOnDestroy() {
    this.toastSubscription.unsubscribe();
  }

  private show() {
    console.log(this.message);
    this.toastElement.style.opacity = 1;
    this.toastElement.style.zIndex = 9999;

    window.setTimeout(() => this.hide(), 2500);
  }

  private hide() {
    this.toastElement.style.opacity = 0;
    window.setTimeout(() => this.toastElement.style.zIndex = 0, 400);
  }
}
