import {Injectable} from '@angular/core';
import {Observable, Subscriber} from 'rxjs';
import {ApiResource} from './bootstrap.service';
import {HttpService} from './http.service';
import {flatMap} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private resource;

  constructor(private httpService: HttpService) {
  }

  observeComplete(observer, key) {
    observer.next(this.resource[key].href);
    observer.complete();
  }

  observeError(observer) {
    observer.next('No links');
    observer.complete();
  }

  getLink(key: string) {
    const self = this;
    return Observable.create((observer: Subscriber<any>) => {
      if (this.resource[key]) {
        self.observeComplete(observer, key);
      } else {
        this.getApiList().subscribe(ApiResponse => {
          self.setApiResource(ApiResponse['_links']);
          if (this.resource[key]) {
            self.observeComplete(observer, key);
          } else {
            self.observeError(observer);
          }
        }, error => {
          self.observeError(observer);
        });
      }
    });
  }

  setApiResource(obj: ApiResource): ApiResource {
    this.resource = obj;
    return this.resource;
  }

  getApiList() {
    return this.httpService.get('/catalog-portal/services/console/api' + window.location.search);
  }

  getTooglesList(): Observable<any> {
    return this.getLink('toggles')
      .pipe(flatMap(url => this.httpService.get(url + window.location.search)));
  }
}
