import {Injectable} from '@angular/core';
import {flatMap, map} from 'rxjs/operators';
import {HttpService} from './http.service';
import {ApiService} from './api.service';

export interface UserDetails {
  adminUser: boolean;
  changePasswordAllowed: boolean;
  emailAddress: string;
  firstName: string;
  lastName: string;
  localUser: string;
  userName: string;
}

@Injectable({
  providedIn: 'root'
})

export class UserProfileService {
  isLoggedIn: boolean;
  userDetails: UserDetails;

  constructor(private httpService: HttpService, private apiService: ApiService) {
  }

  public getUserDetails() {
    const self = this;
    return this.apiService.getLink('consoleUserDetails').pipe(flatMap(url => {
      return self.httpService.get(url + window.location.search);
    }));
  }

  public setUserDetails(details) {
    this.isLoggedIn = details.adminUser;
    this.userDetails = details;
  }

  public doLogOut$() {
    return this.apiService.getLink('consoleLogOut')
      .pipe(map(url => url + window.location.search));
  }

  /**
   * Making the plain XMLHttpRequest to make the call synchronously when the browser tab is closed.
   */
  public doBrowserLogOut$() {
    return this.apiService.getLink('consoleLogOut')
      .pipe(map((url) => {
        const xhr = new XMLHttpRequest();
        xhr.open('GET', url.toString() + window.location.search, false);
        xhr.send();
      }));
  }
}

