import {Component, HostListener} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {Title} from '@angular/platform-browser';
import {UserProfileService} from './core';
import {WindowService} from './core/services/window.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent {
  title = 'app';
  isLoggedInUser = this.userProfileService.isLoggedIn;
  userDetails = this.userProfileService.userDetails;
  isMdmConfigured = this.windowService.getHubConsole().mdmRedirectUrl;
  isErrorMode = this.windowService.getHubConsole().errorMessageCode;

  // We have issues with browser refresh in this logic. It will be fixed in the coming patch.
  // @HostListener('window:beforeunload', ['$event']) public onClick(event: any) {
  //   this.userProfileService.doBrowserLogOut$().subscribe(response => {});
  // }

  constructor(private translate: TranslateService, private titleService: Title, private userProfileService: UserProfileService,
              private windowService: WindowService) {
    translate.setDefaultLang('en');
    translate.use(this.windowService.getHubConsole().i18nLocale);
    this.translate.get('title').toPromise().then(function (title) {
      titleService.setTitle(title);
    });
  }
}
