import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})

export class WindowService {

  private readonly windowObj: object;

  constructor () {
    this.windowObj = window;
  }

  public getWindowObj(): object {
    return this.windowObj;
  }

  public getHubConsole(): HubConsole {
    return this.windowObj['hubConsole'];
  }

  public setHubConsole(obj: HubConsole): void {
    this.windowObj['hubConsole'] = obj;
  }
}

export class HubConsole {
  constructor(
    public mdmRedirectUrl: string,
    public i18nLocale: string,
    public awsCdnHostname: string,
    public errorMessageCode: string
  ) { }
}
